function addressLengthValidator(control: AbstractControl): { [key: string]: boolean } | null {
  const value = control.value as string;
  if (value && value.length > 50) {
    return { 'maxLengthExceeded': true };
  }
  return null;
}

interface State {
  value: string;
  viewValue: string;
}
