  getUserList() {
    this.spinner.show();
    this.crudHttpService.list().subscribe((res: any) => {
      this.filteredData = res.data;  
      this.createUserList = [...this.filteredData].reverse();
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

  nextPage(event) {
    this.spinner.show();
    this.pageNumber = event.pageIndex + 1;
    this.dataService.getAll(this.configuration.user + '?pageNumber=' + this.pageNumber + '&sortBy=createdDate&orderBy=desc').subscribe((data: any) => {
      let userList = data.data;
      this.totalRecords = data.totalRecords;
      this.spinner.hide();
    }, err => {
      this.spinner.hide();
    });
  }

